import React from 'react';
import './ImageShow.css';


// eslint-disable-next-line
export default class ImageShow extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            blnshow: true,
            styledown: {
                backgroundImage: "url(./images/down.svg)",
            }, 
        };
        this.CloseImage = this.CloseImage.bind(this);


    }

    CloseImage() {
        this.setState({ blnshow: false });
    }

    componentWillReceiveProps(newProps) {
        this.setState({ blnshow: true });
    }
    
    componentWillUnmount = () => {
        this.setState = (state,callback)=>{
          return;
        };
    }

    render() {
        var displayResult
        if (this.state.blnshow) {
            displayResult=''
        }
        else{
            displayResult='none'
        }
        var styleMaxWith=document.body.clientWidth > document.body.clientHeight ?  document.body.clientHeight * 0.9 + "px" : "90%"



        return (
                window.document.body.getBoundingClientRect().width > 425?
                <div className="mask-new"  style={{display:displayResult}} onClick={this.CloseImage}>
                    <div className="popup_container" style={{maxWidth:styleMaxWith}} onClick={(e)=>e.stopPropagation()}>
                        <div className="img_ratio">
                            <div className="img_ratio_img" style={{backgroundImage : `url(${this.props.src})`}} onClick={(e)=>e.stopPropagation()}></div>
                        </div>
                        {/* <div className="popup_close">
                            <div className="cross" onClick={this.CloseImage}></div>
                        </div> */}
                        <div className="div-title">{this.props.title}</div>
                        <div className="div-description">{this.props.description}</div>
                    </div>
                </div>
                : <div className="mask-layer-phone" id="maskLayer" style={{display:displayResult}} onClick={this.CloseImage}>
                        <div className="img-container" onClick={(e)=>e.stopPropagation()}>
                            <div className="img-show" style={{backgroundImage : `url(${this.props.src})`}} onClick={(e)=>e.stopPropagation()}></div>
                            <div className="close-box2">
                                <img src="./images/close5.png" onClick={this.CloseImage} />
                            </div>
                            <div className="div-title2">{this.props.title}</div>
                            <div className="div-description2">{this.props.description}</div>
                            
                        </div>
                    </div>
        )
    }
}