import React from 'react';
import './Videoplay.css';
import { Player, BigPlayButton,ControlBar } from 'video-react';
import 'video-react/dist/video-react.css'; // import css

// eslint-disable-next-line
export default class Videoplay extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            blnshow: true,
        };
        this.CloseVideo = this.CloseVideo.bind(this);


    }

    CloseVideo(event) {
        this.setState({ blnshow: false });
        event.nativeEvent.stopImmediatePropagation()
        event.stopPropagation()
    }


    



    componentWillReceiveProps(newProps) {
        this.setState({ blnshow: true });
    }
    
    componentWillUnmount = () => {
        this.setState = (state,callback)=>{
        return;
        };
    }

    render() {
        var video
        if (window.deviceMsg.isAndroid) {
             video=<video src={this.props.src} controls="control" autoPlay className="videowidth" webkit-playsinline="true" x-webkit-airplay="true" playsinline="true" x5-video-player-type="h5" x5-video-orientation="h5" x5-video-player-fullscreen="true" webkit-playsinline preload="auto" controlslist="nodownload" disablePictureInPicture></video>
        }
        else if(window.deviceMsg.isIPhone){
            video=<video src={this.props.src} autoPlay className="videowidth" webkit-playsinline="true" x-webkit-airplay="true" playsinline="true" x5-video-player-type="h5" x5-video-orientation="h5" x5-video-player-fullscreen="true" webkit-playsinline preload="auto"></video>
        }
        else if(window.deviceMsg.isPc){
             video=<video src={this.props.src} autoPlay className="img_ratio_video" webkit-playsinline="true" x-webkit-airplay="true" playsinline="true" x5-video-player-type="h5" x5-video-orientation="h5" x5-video-player-fullscreen="true" webkit-playsinline preload="auto" controlslist="nodownload" disablePictureInPicture></video>
            var styleMaxWith=document.body.clientWidth > document.body.clientHeight ?  document.body.clientHeight * 1.0 + "px" : "90%"

        }
        else{
            video=<video src={this.props.src} autoPlay className="videowidth" webkit-playsinline="true" x-webkit-airplay="true" playsinline="true" x5-video-player-type="h5" x5-video-orientation="h5" x5-video-player-fullscreen="true" webkit-playsinline preload="auto" controlslist="nodownload" disablePictureInPicture></video>
        }
        var displayResult
        if (this.state.blnshow) {
            displayResult=''
        }
        else{
            displayResult='none'
        }
        return (
            window.deviceMsg.isPc ?
            this.state.blnshow?
                <div className="mask-new"   onClick={this.CloseVideo}>
                    <div className="popup_container" style={{maxWidth:styleMaxWith}} onClick={(e)=>e.stopPropagation()}>
                        <div className="img_ratio">
                            {video}
                        </div>
                        {/* <div className="popup_close">
                            <div className="cross" onClick={this.CloseVideo}></div>
                        </div> */}
                        {/* <div className="div-title">{this.props.title}</div>
                        <div className="div-description">{this.props.description}</div> */}
                    </div>
                </div>
                :null
                :
            this.state.blnshow?
            <div className="mask-layer-phone" id="maskLayer"  onClick={this.CloseVideo}>
                <div className="video-box" id="videoBox" onClick={(e)=>e.stopPropagation()}>
                {video}
                </div>
                <div className="close-box2">
                    <img src="./images/close5.png" onClick={this.CloseImage} />
                </div>
            </div>
            :null
        )
    }
}