            console.log(pattern)

            if (!pattern.test(phone)) {
                // alert("手机号码格式不正确，请重填"); 
                prize.textContent = '手机号码格式不正确，请重填!!!';
                prize2.textContent = '手机号码格式不正确，请重填!!!';
                showAlert()
                $('#start').css('cursor', 'pointer')
                return false;
            }
