import React from 'react';
import './ImageShow.css';



// eslint-disable-next-line
export default class LotteryHtml extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            blnshow: false,
        };
        this.CloseImage = this.CloseImage.bind(this);
    }

    componentDidMount() {
        this.setState({ blnshow: this.props.blnshow });
    }



    CloseImage() {
        this.setState({ blnshow: this.props.blnshow });
    }


    componentWillReceiveProps(newProps) {
        this.setState({ blnshow: newProps.blnshow });
    }



    
    componentWillUnmount = () => {
        this.setState = (state,callback)=>{
          return;
        };
    }

    render() {
        var displayResult
        if (this.state.blnshow) {
            displayResult=''
        }
        else{
            displayResult='none'
        }
        console.log(displayResult)
        return (
            <div>
                <div className="mask-layer" id="maskLayer" style={{display:displayResult}} onClick={this.CloseImage}>
                    <div className="divContainer-Lottery" id='lottery' onClick={(e)=>e.stopPropagation()}>
                    </div>
                    <div className="close-box">
                        <img src="./images/close2.svg" onClick={this.CloseImage} />
                    </div>
                </div>
            </div>
        )
    }
}