import React from 'react';
import './QRcode.css';
// eslint-disable-next-line
export default class QRcode extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            strHtml: ""
        }
        this.handleClick = this.handleClick.bind(this);
    }



    componentWillUnmount = () => {
        this.setState = (state,callback)=>{
          return;
        };
    }

    handleClick(event) {
        // this.setState({ strHtml: "" })
        this.props.target.click()
    }

    // componentWillUnmount() {
    //     console.log(123)
    // }





    render() {
        return (
            <div>
                <div className="divbackground4">
                    <div className="divQRcodeContainer4">
                        <img src="./images/close.svg" className="imgclose4" onClick={this.handleClick} />
                        <img src="./images/QRcodescan.png" className="imgQRcode4" />
                        <div className="divcompanyandcontact4">
                            <div className="company"><img src="./images/company.svg" />官方网站：www.geometryauto.com</div>
                            <div className="contact"><img src="./images/phone.svg" />联系方式：400-886-9888</div>
                        </div>
                    </div>
                </div>
            </div>
            
        )
    }
}