function updateEnvironment(strPath){
    const pmremGenerator = new THREE.PMREMGenerator( renderer );
    pmremGenerator.compileEquirectangularShader();
    new RGBELoader()
        .setDataType( THREE.UnsignedByteType )
        .load( strPath, function ( texture ) {
            var radianceMap = pmremGenerator.fromEquirectangular( texture ).texture;
            console.log(radianceMap)
            // radianceMap.rotation.x = Math.PI / 2
            scene.environment = radianceMap;
            // scene.background = radianceMap;

            pmremGenerator.dispose();
            texture.dispose();
        } );



}

function updateEnvironment2(strPath){

    const pmremGenerator = new THREE.PMREMGenerator( renderer );
    pmremGenerator.compileEquirectangularShader();
    new THREE.TextureLoader().load( strPath, function ( texture ) {
        texture.encoding = THREE.sRGBEncoding;
        var pngBackground = pmremGenerator.fromEquirectangular( texture ).texture;
        pngBackground.wrapS = THREE.RepeatWrapping
        pngBackground.wrapT = THREE.RepeatWrapping
        // pngBackground.offset.y=1.0
        pngBackground.offset.x=3.14
        scene.environment = pngBackground;
        pmremGenerator.dispose();
        texture.dispose();
    } );

}





