ThreejsAPI.prototype.createSprite = function (Json) {
    

    var elementAll
    var n=0
    Json.forEach(item => {
        n=n+1
        var point = document.createElement("div");
        var divImage=document.createElement("div");
        var divImage2=document.createElement("div");
        point.appendChild(divImage);
        point.appendChild(divImage2);
        divImage2.style.cssText=item.elementStyle
        divImage2.className = "bg3";
        divImage.className = "bg2";
        // console.log(item.elementStyle)
        var linkElement = document.createElement("div");
        var pointClick = document.createElement("div");
        divImage.setAttribute('id','point'+item.name+n);
        var pointLabel = new CSS2DObject(point);
        pointLabel.visible = false;
        pointLabel.minVisibleDot = item.minVisibleDot
        pointLabel.ctype ="css2dType"
        pointLabel.name =item.name
        pointLabel.pointClick = divImage
        pointLabel.position.set(item.position.x, item.position.y, item.position.z);
        scene.add(pointLabel);
    

        point.addEventListener('touchend', function () {
            clickPoint()
        })

        point.addEventListener('click', function () {
            clickPoint()
        })
        pointClick.addEventListener('click', function (event) {
            pointClickEvent(event)
        });
        pointClick.addEventListener('touchend', function (event) {
            pointClickEvent(event)
        });

        function clickPoint() {

            if (item.blnLink) {
                if (point.innerHTML.indexOf('flag1') > 0) {
                    divImage.innerHTML = ``;
                } else {
                    divImage.appendChild(linkElement)
                    divImage.appendChild(pointClick)
                    linkElement.innerHTML = '<div class="flag"></div>'
                    pointClick.innerHTML = `<div class="flag1">` + item.title + `</div>`;
    
                    for (let index = 0; index < elementAll.length; index++) {
                        const element = elementAll[index];
                        if (element.getAttribute('id')!==divImage.getAttribute('id')) {
                            element.innerHTML = ``
                        }
                    }
                } 
            }
            else{
                pointClickEvent(event)
            }

        }
        function pointClickEvent(event) {
            event.cancelBubble = true;
            item.function(event)
            divImage.innerHTML = ``;
        }

    })

    elementAll=document.getElementsByClassName('bg2')
    //这个一般就这么设置 一般不i便
    labelRenderer = new CSS2DRenderer();
    labelRenderer.setSize(window.innerWidth, window.innerHeight); //setsize
    labelRenderer.domElement.style.position = "absolute";
    labelRenderer.domElement.style.top = 0;
    // labelRenderer.domElement.style.pointerEvents = 'none';  //注意这个 如果orbitcontrol没有添加labelrenderer，domelemnt的话 就没法
    container.appendChild(labelRenderer.domElement);
    // controls = new OrbitControls(camera,labelRenderer.domElement);


}


