ThreejsAPI.prototype.LoadInsideTexture = function (strParentPath, strSuffix,name ) {

    var materials = [];
    // if (mapAlltexture.has(strParentPath + strSuffix)) {
    //     materials = mapAlltexture.get(strParentPath + strSuffix)
    // } else {
    var pngArr = ['r', 'l', 'u', 'd', 'f', 'b'];
    const textures = [];
    for (let index = 0; index < pngArr.length; index++) {
        const element = pngArr[index];
        textures.push(new THREE.TextureLoader().load(strParentPath + element + '.' + strSuffix)) //'./AboutCar/panorama/Lightgrey/'
    }
    for (let i = 0; i < 6; i++) {
        materials.push(new THREE.MeshPhongMaterial({
            map: textures[i],
            transparent: true,
            emissiveMap:textures[i],
            emissive: 0xE1FFFF,
            emissiveIntensity:1.0
        }));
    }
        // mapAlltexture.set(strParentPath + strSuffix, materials)
    // }
    var skyBox = new THREE.Mesh(new THREE.BoxBufferGeometry(10, 10, 10), materials);
    skyBox.geometry.scale(1, 1, -1);
    skyBox.renderOrder=1
    skyBox.name=name
    skyBox.visible=false;
    scene.add(skyBox);

}


