function TweenFog(intNear,intFar,intTime) {
    scene.fog = new THREE.Fog(0xcce0ff, intNear, intFar);
    //new TWEEN.Tween(meshCylinder22.position)
    //        .to( { x: -100 }, 2000 ).repeat( Infinity ).start();
    let tweenSphere = new TWEEN.Tween(scene.fog);
    tweenSphere.to({
        near: 0,
        far: 0
    }, intTime);
    tweenSphere.start();
}



function TweenCamera(current2,target2,intTime,storeVariable){
        
    var changeposition = function (current1, target1, current2, target2,storeVariable) {
        var positionVar = {
            x1: current1.x,
            y1: current1.y,
            z1: current1.z,
            x2: target1.x,
            y2: target1.y,
            z2: target1.z
        };
        var tween = new TWEEN.Tween(positionVar);
        tween.to({
            x1: current2.x,
            y1: current2.y,
            z1: current2.z,
            x2: target2.x,
            y2: target2.y,
            z2: target2.z
        }, intTime);

        tween.onUpdate(function () {
            camera.position.x = positionVar.x1;
            camera.position.y = positionVar.y1;
            camera.position.z = positionVar.z1;
            controls.target.x = positionVar.x2;
            controls.target.y = positionVar.y2;
            controls.target.z = positionVar.z2;
            controls.update();
        })

        tween.onComplete(function () {
            controls.minDistance = storeVariable;

        })
        //https://forum.cocos.org/t/cc-tween-easing/78875/2
        tween.start();
    }


    const current1 = camera.position;
    const target1 = controls.target;
    changeposition(current1, target1, current2, target2,storeVariable);


}

function traverseMaterials (object, callback) {
    object.traverse((node) => {
      if (!node.isMesh) return;
      const materials = Array.isArray(node.material)
        ? node.material
        : [node.material];
      materials.forEach(callback);
    });
  }
  

  ThreejsAPI.prototype.updateEncoding = function () {
    updateTextureEncoding(scene,THREE.sRGBEncoding);
  }


  function updateTextureEncoding (obj,encoding) {
    traverseMaterials(obj, (material) => {
      if (material.map) material.map.encoding = encoding;
      if (material.emissiveMap) material.emissiveMap.encoding = encoding;
      if (material.map || material.emissiveMap) material.needsUpdate = true;
    });
  }


export default ThreejsAPI;